import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { TranslateModule, TranslateLoader } from '@ngx-translate/core'
import { TranslateHttpLoader } from '@ngx-translate/http-loader'
import { HttpClientModule, HttpClient } from '@angular/common/http'
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { MainComponent } from './pages/main/main.component';
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import { WerkeComponent } from './pages/werke/werke.component';
import { NachlassComponent } from './components/nachlass/nachlass.component';
import { StickynavbarComponent } from './components/stickynavbar/stickynavbar.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { CatalogEntryComponent } from './components/catalog-entry/catalog-entry.component';
import { StringcapPipe } from './pipes/stringcap.pipe';
import { PrintEnDePipe } from './pipes/print-en-de.pipe';
import { BibliothekComponent } from './pages/bibliothek/bibliothek.component'

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}


@NgModule({
  declarations: [
    AppComponent,
    MainComponent,
    HeaderComponent,
    FooterComponent,
    WerkeComponent,
    NachlassComponent,
    StickynavbarComponent,
    CatalogEntryComponent,
    StringcapPipe,
    PrintEnDePipe,
    BibliothekComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateLoader,
        deps: [HttpClient]
      }
    }),
    FontAwesomeModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
